import { Avatar, IconButton } from "@material-ui/core";
import React, { useState, useEffect } from "react";
import "./MessageSender.css";
import VideocamIcon from "@material-ui/icons/Videocam";
import PhotoLibraryIcon from "@material-ui/icons/PhotoLibrary";
import InsertEmoticonIcon from "@material-ui/icons/InsertEmoticon";
import { useStateValue } from "./StateProvider";
import ImageIcon from "@material-ui/icons/Image";
import { Fab } from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";
import db from "./firebase";
import firebase from "firebase";
import { Label } from "@material-ui/icons";

function MessageSender() {
  const [{ user }, dispatch] = useStateValue();
  const [input, setInput] = useState("");
  const [imageUrl, setImageUrl] = useState("");
  const [url, setUrl] = useState("");
  const [path, setPath] = useState("");

  useEffect(() => {
    if (url) {
      db.collection("posts")
        .add({
          message: input,
          timestamp: firebase.firestore.FieldValue.serverTimestamp(),
          profilepic: user.photoURL,
          username: user.displayName,
          image: url,
          likes: { 1: user.uid },
          idPost: "",
        })
        .then(function (docRef) {
          db.collection("posts")
            .doc(docRef.id)
            .set({
              message: input,
              timestamp: firebase.firestore.FieldValue.serverTimestamp(),
              profilepic: user.photoURL,
              username: user.displayName,
              image: url,
              likes: { 1: user.uid },
              idPost: docRef.id,
            });
        });

      /* 
db.collection('likes').add({
  
  profilepic:user.photoURL,
  username:user.displayName,

  
  }) */
    }
  }, [url]);

  //db.collection('posts').doc().add({likes})

  //firebase.database().ref('posts').update({likes:{}})

  console.log(imageUrl);
  console.log(path);

  const PostDetails = (e) => {
    const data = new FormData();
    data.append("file", imageUrl);
    data.append("upload_preset", "insta-clone");
    data.append("cloud_name", "esprit");
    fetch("https://api.cloudinary.com/v1_1/hamma/image/upload", {
      method: "post",
      body: data,
    })
      .then((res) => res.json())
      .then((data) => {
        setUrl(data.url);
      })
      .catch((err) => {
        console.log(err);
      });
    e.preventDefault();
  };
  return (
    <div className="messagesender">
      <div className="messagesender_top">
        <Avatar src={user.photoURL} />

        <form>
          <input
            className="messagesender_input"
            value={input}
            onChange={(e) => setInput(e.target.value)}
            placeholder={`what'S on your mind,${user.displayName} ?`}
          />
          {imageUrl ? (
            <input
              className="messagesender_input"
              placeholder={path}
              value={path}

              /*<div className="file-field input-field">
<div className="btn #46b5f6 blue darken-1">
<span>Upload image</span>
<input type="file" onChange={(e)=>setImage(e.target.files[0])} />
</div>
<div className="file-path-wrapper">
<input className="file-path validate" type="text"/>
</div>
</div>*/
            />
          ) : (
            <input style={{ display: "none" }} />
          )}

          <label htmlFor="upload-photo">
            <input
              style={{ display: "none" }}
              id="upload-photo"
              name="upload-photo"
              type="file"
              onChange={(e) => {
                setImageUrl(e.target.files[0]);
                setPath(e.target.value);
              }}
            />
            <Fab color="primary" size="small" component="span" aria-label="add">
              <AddIcon />
            </Fab>
          </label>

          <button onClick={PostDetails}>Hidden submit</button>
        </form>
      </div>
      <div className="messagesender_bottom">
        <div className="messagesender_option">
          <VideocamIcon style={{ color: "red" }} />
          <h3>Live Video</h3>
        </div>
        <div className="messagesender_option">
          <PhotoLibraryIcon style={{ color: "green" }} />
          <h3>Photo Video</h3>
        </div>
        <div className="messagesender_option">
          <InsertEmoticonIcon style={{ color: "orange" }} />
          <h3>Feeling Activity</h3>
        </div>
      </div>
    </div>
  );
}

export default MessageSender;
