import { Avatar ,IconButton} from "@material-ui/core";
import { ThumbDown, ThumbUp } from "@material-ui/icons";
import React, { forwardRef,useState } from 'react';
import ThumbUpIcon from '@material-ui/icons/ThumbUp';
import ThumbDownIcon from '@material-ui/icons/ThumbDown';
import ChatBubbleOutlineIcon from '@material-ui/icons/ChatBubbleOutline';
import NearMeIcon from '@material-ui/icons/NearMe';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import ExpandMoreOutlinedIcon from '@material-ui/icons/ExpandMoreOutlined';
import "./Post.css";
import db from "./firebase";
import firebase from 'firebase'
import { useStateValue } from "./StateProvider";


const Post= forwardRef(({ profilepic, image, username, timestamp, message,idPost }, ref) =>{
  const [i, setI] = useState(0)
  const [{user}, dispatch] = useStateValue(); 
console.log(idPost)

const Like=()=>{
  
  const docRef = db.collection('posts').doc(idPost);
 
  const increment = firebase.firestore.FieldValue.increment(1);
  /* 
  docRef.onSnapshot((snapshot) =>

Object.keys(snapshot.data().likes).map(function()  {
  setI(i+1)
  
}      )) */


db.collection('posts').doc(idPost).update({
     
  
     likes:increment,
     username:user.displayName
  
  
  
})

/* 
  docRef.get().then(function(mypost) {

  
    db.collection('posts').doc(mypost.data().idPost).update({
     
      likes:{5:user.uid}
      
    })
    
  }) */

}


  return (
    <div ref={ref} className="post">
      <div className="post_top">
        <Avatar src={profilepic} className="post_avatar" />
        <div className="post_topInfo">
          <h3>{username}</h3>
          <p>
            {new Date(timestamp?.toDate()).toUTCString()}
          </p>
        </div>
      </div>

      <div className="post_bottom">
        <p>{message}</p>
      </div>
      <div className="post_image">
          <img className={image ? "post_image_imgs" :  "post_image_img"} src={image} alt=""/>
      </div>

      <div className="post_options">

      <div className="post_option">
      <IconButton onClick={Like}>
<ThumbUpIcon/>
</IconButton>
<p>Like</p>
      </div>
      <div className="post_option">
<ChatBubbleOutlineIcon/>
<p>Coment</p>
      </div>
      <div className="post_option">
<NearMeIcon/>
<p>Share</p>
      </div>
      <div className="post_option">
<AccountCircleIcon/>
<ExpandMoreOutlinedIcon/>
      </div>
      </div>
    </div>
  );
})

export default Post;
